<template>
    <div v-if="conditions" class="container">
        <div class="section">Scanning Conditions</div>
        <p class="condition">
            <condition-icon class="icon" :fulfilled="conditions.lighting" />
            Lighting
        </p>
        <p class="condition">
            <condition-icon class="icon" :fulfilled="conditions.distance" />
            {{ distance }}
        </p>
        <p class="condition">
            <condition-icon class="icon" :fulfilled="conditions.centered" />
            Centered
        </p>
        <p class="condition">
            <condition-icon class="icon" :fulfilled="conditions.movement" />
            Movement
        </p>
        <p class="condition">
            <condition-icon class="icon" :fulfilled="conditions.frameRate" />
            Frame Rate
        </p>
        <p class="condition">
            <condition-icon class="icon" :fulfilled="conditions.serverReady" />
            Server Ready
        </p>
    </div>
</template>

<script lang="ts">
import {DistanceCondition, ScanConditions} from "vue-vital-sign-camera";
import {PropType, defineComponent} from "vue";
import ConditionIcon from "./ConditionIcon.vue";

export default defineComponent({
    setup() {

    },
    props: {
        conditions: Object as PropType<ScanConditions>
    },
    components: {
        ConditionIcon
    },
    computed: {
        distance() {
            if (this.conditions === undefined) {
                return "Distance"
            }
            if (this.conditions.distanceCondition == DistanceCondition.TooClose) {
                return "Too close"
            }
            if (this.conditions.distanceCondition == DistanceCondition.TooFar) {
                return "Too far"
            } else {
                return "Distance"
            }
        }
    }
})
</script>

