<template>
  <div id="screen">
    <VitalSignCamera 
      ref="vscam" 
      :device="selectedDevice"
      class="camera" 
      :isActive="isActive"
      :userInfo="userInfo"
      :config="config"
      :visualizationOptions="visualizationOptions"
      @onVideoFrameProcessed="onVideoFrameProcessed"
      @onCameraDevicesUpdated="onCameraDevicesUpdated"
      @onInitialized="onInitialized"
      @onError="onError"
    />
    <div class="loading" v-if="!initialized"><span class="loader"></span>Loading...</div>
    <button class="startButton startText" v-if="healthResult && healthResult.stage == 3" @click="startScanning" :disabled="!disabled"> Start </button>
    <status v-if="healthResult && healthResult.stage !== 3" :healtResult="healthResult"/>
    <condition-checklist v-if="conditions !== undefined && healthResult?.health === undefined" :conditions="conditions" />
    <div class="settings" v-if="healthResult?.stage == 3">
      <fieldset id="options">
        <legend>Visualization</legend>
        <div>
          <input type="checkbox" id="boundingBox" name="boundingBox" v-model="boundingBoxEnabled"/>
          <label for="boundingBox">Bounding Box</label>
        </div>
        <div>
          <input type="checkbox" id="faceMesh" name="faceMesh" v-model="faceMeshEnabled"/>
          <label for="faceMesh">Face Mesh</label>
        </div>
        <div>
          <input type="checkbox" id="animateFaceMesh" name="animateFaceMesh" v-model="animateFaceMesh"/>
          <label for="faceMesh">Animate Face Mesh</label>
        </div>
      </fieldset>
      <select class="cameraSelection" v-model="selectedDevice">
        <option v-for="device in devices" :value="device.id" v-bind:key="device.id">{{ device.label }}</option>
      </select>
    </div>
    <health-result-view  v-if="healthResult?.health" :healthResult="healthResult?.health" :showResult="showResult" @hideResult="hideResult"/>
    <div class="error" v-if="error">{{error}}<button @click="clearError">Dismiss</button></div>
  </div>
</template>

<script lang='ts'>
import { defineComponent } from 'vue'
import { VitalSignCamera } from "vue-vital-sign-camera";
import Status from "./StatusView.vue";
import {
  ScanConditions, 
  VideoFrameProcessedEvent, 
  GetHealthResult, 
  VideoFrameInfo,
  NormalizedFacebox, 
  UserInfo, 
  Gender,
  ServerId, 
  CameraDevicesUpdatedEvent,
  CameraDevice,
  VisualizationOptions,
} from "vue-vital-sign-camera";
import ConditionChecklist from "./ConditionChecklist.vue";
import HealthResultView from "./HealthResultView.vue";
import { NormalizedLandmarkList } from '@mediapipe/drawing_utils'

export default defineComponent( {
  data() {
    return {
      isActive: false, // can be used in a button to turn on/off the camera
      healthResult: undefined as GetHealthResult|undefined,
      frameSize: undefined as VideoFrameInfo|undefined,
      facebox: undefined as NormalizedFacebox|undefined,
      conditions: undefined as ScanConditions|undefined,
      userInfo: {
        age: 30,
        gender: Gender.Male,
        userId: '07a51b41-e1a4-4570-94a7-04ef371cc4f3',
        waistCircumference: 30,
        weight: 50,
        height: 170,
      } as UserInfo,
      showResult: false,
      disabled: false,
      config: {
        serverId: ServerId.AwsProdEnterprise,
        apiKey: "3m46C76Tka5wfpsxhZZQk1nhE49I9smM5zTag7PN",
      },
      landmarks: undefined as NormalizedLandmarkList|undefined,
      devices: [] as CameraDevice[],
      selectedDevice: undefined as string | undefined,
      initialized: false,
      boundingBoxEnabled: true,
      faceMeshEnabled: true,
      animateFaceMesh: true,
      error: undefined as unknown,
    }
  },
  components: {
    VitalSignCamera,
    Status,
    ConditionChecklist,
    HealthResultView,
  },
  computed: {
    visualizationOptions(): VisualizationOptions {
      return {
        boundingBox: {color:'red', enabled: this.boundingBoxEnabled},
        faceMesh: {color:'lightgreen', enabled: this.faceMeshEnabled, animate: this.animateFaceMesh}
      }
    }
  },
  methods: {
    startScanning() {
      const vscam:InstanceType<typeof VitalSignCamera> = this.$refs.vscam as InstanceType<typeof VitalSignCamera>;
      vscam.startScanning();
      this.showResult = true;
    },
    onVideoFrameProcessed(processedVideo:VideoFrameProcessedEvent) {
      const {videoFrameInfo, facebox, healthResult, scanConditions, landmarks} = processedVideo;
      this.conditions = scanConditions;
      this.healthResult = healthResult;
      this.facebox = facebox;
      this.frameSize = videoFrameInfo;
      this.landmarks = landmarks;
      this.disabled = this.conditions !== undefined 
        && this.conditions.centered && this.conditions.distance && this.conditions.serverReady 
        && this.conditions.frameRate && this.conditions.lighting && this.conditions.movement;
    },
    onCameraDevicesUpdated(event:CameraDevicesUpdatedEvent) {
      this.devices = event.devices
      if (event.selected === undefined || event.selected === '') {
        this.selectedDevice = event.devices[0].id
      } else {
        this.selectedDevice = event.selected
      }
    },
    onInitialized() {
      this.initialized = true;
    },
    onError(error:unknown) {
      this.error = error;
    },
    clearError() {
      this.error = undefined;
    },
    hideResult() {
      this.showResult = false;
      this.healthResult && (this.healthResult.health = undefined);
    },
  },
  async mounted() {
    this.isActive = true;
  },
})
</script>

